
import { checkSize, checkMode, calculateHash } from './until';
import {
    READY,
    PARSE,
    UPLOAD,
    PAUSED,
    FINISH,
    DESTROY,
    FAIL,
    PARALLEL,
    SERIAL
} from './const'
export default class ChunkUploadFile {
    // åå§åä¼ å¥size,mode
    constructor({ size, mode,accept=[] } = {}) {
        this.chunkSize = checkSize(size) ? size : 1 * 1024 * 1024
        this.accept =(accept&& accept.length>0)?accept.join(','):''
        this.fileId = ''//æä»¶hashå¼
        this.hashProgress = function () { }//è®¡ç®hashçè¿åº¦å½æ°
        // this.uploadProgress = function () { }//ä¸ä¼ çè¿åº¦å½æ°
        this.fileChunkList = []//åçåçæä»¶åè¡¨
        this.file = null//æä»¶
        this.fileName = ''
        this.fileSize = ''
        this.fileType = ''
        this.chunkLength = 0
        this.errIds = {}//ä¸ä¼ éè¯¯çä¸æ 
        this.sucIds = {}//ä¸ä¼ æåçä¸æ 
        this.paused = false//æåflag
        this.mode = checkMode(mode) ? mode : SERIAL// å¹¶è¡:parallelï¼ä¸²è¡ï¼serial
        this.customReq = null //ç¨äºç¼å­ç¨æ·çèªå®ä¹ä¸ä¼ è¯·æ±
        this.state = READY//ç¶æ ready parse upload paused finish destroy fail
    }
    // è·åæä»¶
    async readFile() {
        if(this.state === DESTROY){
            console.error('js-chunk-file-uploadå®ä¾å·²ç»è¢«éæ¯');
            return false
        }
        // éç½®æ°æ®
        this.resetData()
        this.file = await this.showFileWindow(this.needType)
        // æ¹åç¶æ
        this.state = PARSE
        // è®¾ç½®å¼
        const { name, type, size } = this.file
        this.fileName = name
        this.fileType = type
        this.fileSize = size
        this.fileId = await calculateHash(this.file, this.hashProgress)
        this.fileChunkList = await this.chunkFile(this.file)
        this.onFileReadEnd(this.file, this.fileId)
        return new Promise((resolve, reject) => {
            resolve(this.fileId)
        })
    }
    // åçä¸ä¼ æä»¶
    async sendFile({ customReq, savedChunkIds }) {
        if(this.state === DESTROY){
            console.error('js-chunk-file-uploadå®ä¾å·²ç»è¢«éæ¯');
            return false
        }
        this.state = UPLOAD
        if (customReq) {
            this.customReq = customReq
        }
        // å»é¤å·²ç»å­å¨ç
        if (savedChunkIds && savedChunkIds.length > 0) {
            savedChunkIds.map(i => {
                if (this.sucIds[i] === undefined || this.sucIds[i] === null) {
                    this.sucIds[i] = i
                }
            })
        }
        this.uploadChunks(this.fileChunkList, this.customReq)
    }
    // æå¼æä»¶æ¡
    showFileWindow() {
        return new Promise((resolve) => {
            /* å¨åå­ä¸­åå»ºä¸ä¸ªinputå¯¹è±¡ï¼æ éæ³¨å¥DOMï¼ */
            const input = document.createElement('input')
            /* æ¹ä¸ºæä»¶æ¨¡å¼ */
            input.type ='file'
            /*å å¥æ¥åæä»¶æ ¼å¼*/ 
            if(this.accept){
                input.accept = this.accept
            }
            /* å¤éæ¨¡å¼ */
            input.multiple = true
            /* å®ä¹æä»¶éæ©çå¬ */
            input.onchange = function (e) {
                /* åå§åæä»¶å®¹å¨ */
                const Files = Array.from(e.target.files)
                resolve(Files[0])
            }
            input.click()
        })
    }
    // åçæä»¶
    async chunkFile(file, size = this.chunkSize) {
        const fileChunkList = [];
        let curChunkIndex = 0;
        let fileIndex = 0
        const chunkLength = Math.ceil(file.size / size)
        this.chunkLength = chunkLength
        while (curChunkIndex <= file.size) {
            const chunk = file.slice(curChunkIndex, curChunkIndex + size);
            // åè£æä»¶
            fileChunkList.push({ file: chunk, index: fileIndex, chunkId: fileIndex, md5: this.fileId, chunkLength, name: this.fileName, size: chunk.size })
            curChunkIndex += size;
            fileIndex++
        }
        return new Promise((resolve, reject) => {
            resolve(fileChunkList)
        })

    }
    // åè£è¯·æ±ï¼å®ç°èªå®ä¹è¯·æ±
    _packReq(chunkId, file, customReq) {
        const that = this
        // å°fileçä¿¡æ¯å­ä¸ºç§æå¼ï¼æ¹ä¾¿åç»­è®°å½
        let _data = { chunkId: file.chunkId, md5: file.md5 }
        return new Promise((resolve, reject) => {
            const next = () => {
                that.sucIds[_data.chunkId] = _data.chunkId
                let sucLen = Object.keys(that.sucIds).length
                let precent = (sucLen / that.chunkLength).toFixed(3)
                that.onUpload(precent, _data.chunkId)
                // åæ¶_data
                _data = null
                resolve()
            }
            customReq(file, next, _data)
        })


    }
    // ä¸ä¼ åç
    async uploadChunks(fileList, customReq) {
        if (customReq) {
            if (this.mode === SERIAL) {
                
                for (let index = 0; index < fileList.length; index++) {
                    if(this.state === DESTROY){
                        return false
                    }
                    const file = fileList[index];
                    if (!(this.sucIds[file.chunkId] === undefined || this.sucIds[file.chunkId] === null)) {
                        continue
                    }
                    if (this.paused) {
                        return false
                    }
                    await this._packReq(file.chunkId, file, customReq)

                }
                this.state = FINISH
                this.onFullUpload(this.fileId)
            } else {
                const reqList = []
                fileList.map((file) => {
                    if (this.sucIds[file.chunkId] === undefined || this.sucIds[file.chunkId] === null) {
                        reqList.push(
                            this._packReq(file.chunkId, file, customReq)
                        )
                    }
                })
                Promise.all(reqList).then(() => {
                    this.state = FINISH
                    this.onFullUpload(this.fileId)
                })
                    .catch((err) => {
                        if (err !== PAUSED) {
                            this.state = PAUSED
                        }
                    })
            }
        } else {
            throw new Error('ç¼ºå¤±è¯·æ±æ¹æ³')
        }
    }
    // åè£å½ä¸ªåçæä»¶
    packgeChunk(file) {
        // åå»ºæ°æ®å®¹å¨
        const formdata = new FormData();
        Object.keys(file).map(key => {
            formdata.append(key, file[key])
        })

        /* å°æ°æ®æ³¨å¥CloudChunkå®ä¾å¯¹è±¡ */
        return formdata;
    }
    // æåä¸ä¼ 
    pauseUpload() {
        if(this.state === DESTROY){
            console.error('js-chunk-file-uploadå®ä¾å·²ç»è¢«éæ¯');
            return false
        }
        if (this.mode === SERIAL) {
            this.paused = true
            this.state = PAUSED
            this.onPaused()
        } else {
            console.error('å¹¶è¡æ¨¡å¼ä¸è½ä½¿ç¨æååè½')
        }
    }
    // ç»§ç»­ä¸ä¼ 
    continueUpload() {
        if(this.state === DESTROY){
            console.error('js-chunk-file-uploadå®ä¾å·²ç»è¢«éæ¯');
            return false
        }
        if (this.mode === SERIAL) {
            this.paused = false
            this.state=UPLOAD
            this.sendFile({ fileId: this.fileId, customReq: this.customReq })
            this.onContinue()
        } else {
            console.error('å¹¶è¡æ¨¡å¼ä¸è½ä½¿ç¨æååè½')
        }
    }
    destroy() {
        this.state = DESTROY
        this.onDestroy()
    }
    restart(){
        this.resetData()
        this.onRestart()
    }
    // é©å­å½æ°
    onFileReadEnd(file, fileId) {

    }
    onUpload(progress, index) { }
    onFullUpload(fileId) {
    }
    onDestroy() { }
    onRestart() { }
    onPaused() { }
    onContinue() { }
    resetData() {
        this.fileId = ''//æä»¶hashå¼
        this.hashProgress = function () { }//è®¡ç®hashçè¿åº¦å½æ°
        // this.uploadProgress = function () { }//ä¸ä¼ çè¿åº¦å½æ°
        this.fileChunkList = []//åçåçæä»¶åè¡¨
        this.file = null//æä»¶
        this.fileName = ''
        this.fileSize = ''
        this.fileType = ''
        this.errIds = {}//ä¸ä¼ éè¯¯çä¸æ 
        this.sucIds = {}//ä¸ä¼ æåçä¸æ 
        this.paused = false//æåflag
        this.customReq = null //ç¨äºç¼å­ç¨æ·çèªå®ä¹ä¸ä¼ è¯·æ±
        this.state=READY
    }
}
